import { Button, Card, Carousel, Col, message, Row, Space, Timeline, Typography, Divider } from 'antd'
import React, { Component } from 'react'
import offering_image from './1.png'
import { howitworks, landing, offerings, testimonials } from './data'
import './Home.sass'
import LeadCollectionForm from './LeadCollectionForm'
import landing_image from './landing.png'

const { Meta } = Card
const { Paragraph, Title } = Typography

class Home extends Component {
	constructor(props) {
		super(props)
		this.state = {
			visible: false
		}
	}

	onCreate = values => {
		//console.log('Received values of form: ', values)
		message.success('Thank you for showing interest. We will reach out to you shorty.')
		this.setState({
			visible: false
		})
	}

	render() {
		const { visible } = this.state
		return (
			<React.Fragment>
				<Row
					align="middle"
					className="container-row"
					justify="center"
					key="1"
					style={{
						backgroundImage: `url(${landing_image})`,
						backgroundSize: 'cover'
					}}
				>
					<div className="overlay"></div>
					<Space
						align="center"
						direction="vertical"
						size="large"
					>
						<Title>{landing.title.toUpperCase()}</Title>
						<Title
							level={4}
							style={{ textAlign: 'left' }}
						>
							{landing.sub_title}
						</Title>
						<Paragraph className="para">{landing.description}</Paragraph>
						<Button
							onClick={() => {
								this.setState({
									visible: true
								})
							}}
							shape="round"
							size="large"
							type="primary"
						>
							{landing.btn_title.toUpperCase()}
						</Button>
					</Space>
				</Row>
				<Row
					className="container-row"
					id="howitworks"
					justify="center"
				>
					<Col
						md={16}
						xs={20}
						style={{ textAlign: 'center' }}
					>
						<Row justify="center">
							<Space
								direction="vertical"
								size="large"
								align="center"
							>
								<Title level={2}>{howitworks.title.toUpperCase()}</Title>
								<Row justify="center">
									<Col
										md={16}
										xs={20}
									>
										<Timeline mode="alternate">
											{
												howitworks.content.map((item, index) => (
													<Timeline.Item
														dot={<img alt="" src={`${process.env.PUBLIC_URL}/assets/images/hiw_${index + 1}.png`} width="50" />}
														key={`howitwork-${index}`}
													>
														<Space direction="vertical">
															<Title level={4}>{item.title}</Title>
															<Paragraph>{item.sub_title}</Paragraph>
															{
																item.btn_title && <Button
																	onClick={() => {
																		this.setState({
																			visible: true
																		})
																	}}
																	shape="round"
																	type="primary"
																>
																	{item.btn_title.toUpperCase()}
																</Button>
															}
														</Space>
													</Timeline.Item>
												))
											}
										</Timeline>
									</Col>
								</Row>
							</Space>
						</Row>
					</Col>
				</Row>
				<Row
					align="middle"
					className="container-row"
					id="offerings" 
					justify="center"
					key="3"
				>
					<Col
						md={16}
						xs={20}
					>
						<Space
							align="middle"
							direction="vertical"
							size="large"
						>
							<Title level={2} style={{ textAlign: 'center' }}>{offerings.title.toUpperCase()}</Title>
							{
								offerings.content.map((offering, index, arr) => (
									<React.Fragment key={`offering-${index}`}>
										{
											offering.sub_offerings ?
												<Space
													align="center"
													direction="vertical"
													size="large"
												>
													<Row
														justify="center"
													>
														<Col
															md={12}
															xs={20}
														>
															<Card
																bodyStyle={{ padding: 0, textAlign: 'center' }}
																bordered={false}
																style={{ background: 'transparent' }}
															>
																<Meta
																	title={<Title level={3}>{offering.title}</Title>}
																	description={
																		offering.sub_title
																	}
																/>
															</Card>
														</Col>
													</Row>
													<Space
														direction="vertical"
														size="large"
													>
														{
															offering.sub_offerings.map((sub_offering, subindex) => (
																<Row
																	align="middle"
																	gutter={[48, 48]}
																	justify="center"
																	key={`sub_offering-${subindex}`}
																>
																	<Col
																		md={16}
																		push={subindex % 2 === 0 ? 8 : 0}
																		xs={20}
																	>
																		<Card
																			bodyStyle={{ padding: 0 }}
																			bordered={false}
																			cover={
																				<img
																					alt="sample"
																					src={`${process.env.PUBLIC_URL}/assets/images/offerings_${index + 1}_${subindex + 1}.png`}
																					style={{ boxShadow: '0 .59rem .98rem rgba(184,173,209,.6)' }}
																				/>
																			}
																			style={{ background: 'transparent' }}
																		/>
																	</Col>
																	<Col
																		md={8}
																		pull={subindex % 2 === 0 ? 16 : 0}
																		xs={20}
																	>
																		<Card
																			bodyStyle={{ padding: 0 }}
																			bordered={false}
																			style={{ background: 'transparent' }}
																		>
																			<Meta
																				title={<Title level={3}>{sub_offering.title}</Title>}
																				description={sub_offering.sub_title}
																			/>
																		</Card>
																	</Col>
																</Row>
															))
														}
													</Space>
												</Space> :
												<Row
													gutter={[48, 48]}
													justify="center"
													key={`offering-${index}`}
													align="middle"
												>
													<Col
														md={16}
														push={index % 2 !== 0 ? 8 : 0}
														xs={20}
													>
														<Card
															bodyStyle={{ padding: 0 }}
															bordered={false}
															cover={
																<img
																	alt="sample"
																	src={`${process.env.PUBLIC_URL}/assets/images/offerings_${index + 1}.png`}
																	style={{ boxShadow: '0 .59rem .98rem rgba(184,173,209,.6)' }}
																/>
															}
															style={{ background: 'transparent' }}
														/>
													</Col>
													<Col
														md={8}
														pull={index % 2 !== 0 ? 16 : 0}
														xs={20}
													>
														<Card
															bodyStyle={{ padding: 0 }}
															bordered={false}
															style={{ background: 'transparent' }}
														>
															<Meta
																title={<Title level={3}>{offering.title}</Title>}
																description={
																	offering.sub_title
																}
															/>
														</Card>
													</Col>
												</Row>
										}
										<Row justify="center">
											{arr.length - 1 !== index && <Divider style={{ minWidth: '60%', width: '60%' }} />}
										</Row>
									</React.Fragment>

								))
							}
						</Space>
					</Col>
				</Row>
				<Row
					align="middle"
					className="container-row"
					id="testimonials"
					justify="center"
					key="4"
				>
					<Col span="12">
						<Title level={2} style={{ textAlign: "center", marginBottom: 24 }}>{testimonials.title.toUpperCase()}</Title>
						<Carousel autoplay>
							{
								testimonials.content.map((testimonial, index) => (
									<Card key={`testimonial-${index}`}>
										<Title level={3}>{testimonial.title}</Title>
										<Paragraph>{testimonial.sub_title}</Paragraph>
									</Card>
								))
							}
						</Carousel>
					</Col>
				</Row>
				<LeadCollectionForm
					visible={visible}
					onCreate={this.onCreate}
					onCancel={() => {
						this.setState({
							visible: false
						})
					}}
				/>
			</React.Fragment>
		)
	}
}
export default Home